@import url(https://fonts.googleapis.com/css?family=Baloo+Da+2|Raleway&display=swap);
* {
	font-family: 'Baloo Da 2', cursive, 'Raleway', sans-serif;
	font-size: 2rem;
}

.footer .button {
	margin: 0.75rem;
}

@media (min-width: 320px) and (max-width: 1024px) {
	.pull-up {
		margin-top: -5rem;
	}
}

@media (min-width: 1025px) {
	.pull-up {
		margin-top: -10rem;
	}
}

.card {
	border-radius: 1rem;
}
